*{
  margin: 0px;
  padding: 0px;
  outline: none;
  box-sizing: border-box;
}
nav{
  width: 100%;
  height: 15vh;
  background: linear-gradient(rgba(2, 2, 2, 0.925),rgba(0, 174, 255, 0.536));
  display: flex;
  align-items: center;
  text-align: center;
  justify-content: space-evenly;
  
}
nav a{
  font-size: 18px ;
  text-decoration: none;
  color: #ffffff94;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  font-weight: bolder;
}
nav a:hover{
  font-size: 19px;
  color: rgb(0, 0, 0);
}
.ab{
  font-size: 18px ;
  text-decoration: none;
  color: black;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  font-weight: bolder;
}
.c0{
  width: 100%;
  height: 75vh;
  display: flex;
  justify-content: center;
  overflow: scroll;
  scrollbar-width: none;
}
.c1{
display: flex;
flex-direction: column;
width: 135%;
height: 500px;
align-items: center;
justify-content: center;
background: linear-gradient(rgba(0, 174, 255, 0.165),rgba(0, 174, 255, 0.488));
border-radius: 4px;
border: 2px  rgba(0, 183, 250, 0.41);
border-style: groove;
padding: 15px;
margin-left: 45%;
margin-top: 45%;
}
.c2{
  display: flex;
  flex-direction: column;
  width: 78%;
  height: 73vh;
  text-align: center;
  padding: 70px;
  margin-left: 10%;
  margin-top: -13px;
}
.r{
  display: flex;
  align-items: center;
  margin-top: 8px;
  font-size: small;
  gap: 5px;

}
.r input[type="radio"]{
  margin: 0;
}

.butt{
  display: flex;
  gap: 20px;  
}
.butt button:hover{
  color: rgb(0, 0, 0);
  background: linear-gradient(rgba(0, 174, 255, 0.361),rgba(0, 174, 255, 0.887));
}
.tbs {
  overflow: scroll;
  font-size: 12px;
  scrollbar-width: none;
}

th{
  background: linear-gradient(rgba(0, 174, 255, 0.324),rgba(0, 174, 255, 0.603));
  padding: 15.9px;  
  border-radius: 5px;
}
td{
  border-radius: 5px;
  background: linear-gradient(rgba(0, 174, 255, 0.139),rgba(0, 174, 255, 0.433));
  padding: 10px;
  height: 2vh;
}
.c1 input{
  font-size: 11px;
  padding: 8px;
  margin-top: 8px;
  border: none;
  width: 100%;
  border-radius: 5px;
}
h2{
  margin-top: -30px;
  margin-bottom: 30px;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}
.c1 button{
  margin-top: 12px;
  border: none;
  color: rgb(0, 0, 0);
  font-size: small;
  background-color:rgba(1, 64, 93, 0.622);
  padding: 8px;
  cursor: pointer;
  border-radius: 8px;
}

.gt{
  margin: auto;
  padding: 4%;
  align-items: center;
  justify-content: center;
}
.gt td,th{
  height: 3vh;
}
.get{
  height: 75vh;
  overflow: scroll;
  scrollbar-width: none;
}
.v8{
 display: flex;
 justify-content: center;
 gap: 10px;
 margin-top: 45px;
 padding: 8px;
}
.v8 input{
  border-top: none;
  border-left: none;
  border-right: none;
  padding: 4px;
  font-size: 16px;
}
.v8 button{
  border: none;
  background-color:rgba(1, 64, 93, 0.622);
  padding: 8px;
  cursor: pointer;
  border-radius: 8px;
}
.r1{
  text-align: center;
  padding: 10px;
  color: rgb(0, 0, 0);
}
