.b0{
  display: flex;
  text-align: center;
  align-items: center;
  justify-content: center;
  width: 100%;
  flex-direction: column;
  height: 75vh;
  background-color: aliceblue;
}

.b2{
  width: 300px;
  border-radius: 5%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 50px;
  height: 350px;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.274);
  background: linear-gradient(white,rgba(0, 174, 255, 0.212));
}
.b2 input{
  padding: 8px;
  margin-top: 8px;
  border-color:rgba(0, 174, 255, 0.656);
  background-color: rgb(255, 255, 255);
  border-radius: 5px;
  font-size: 15px;
}
.b2 button{
  border: 2px solid;
  border-color:rgba(0, 174, 255, 0.656);
  background-color: white;
  margin-top: 15px;
  padding: 8px;
  border-radius: 8px;
  font-size: 14px;
  width: 89%;
  
}
.b2 button:hover {
  box-shadow: 0px 6px 10px rgba(0, 0, 0, 0.2);
  transform: translateY(-3px);
}

.b2 button:active {
  box-shadow: 0px 3px 4px rgba(0, 0, 0, 0.2); 
  transform: translateY(1px); 
}
.b2 button:hover{
  background-color: rgba(0, 174, 255, 0.656);;
  color: rgba(255, 255, 255, 0.988);
  cursor: pointer;
}
.err{
  text-align: center;
  align-items: center;
  margin-top: 20px;
  color: red;
  font-family: 'Courier New', Courier, monospace;

}
.about{
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 20px;
  font-size: 16;
  
}
.fo{
  display: flex;
  justify-content: space-evenly;
  background: linear-gradient(rgba(0, 174, 255, 0.536),rgba(2, 2, 2, 0.956));
  text-align: center;
  height: 10vh;
  align-items: center;
}
.fo a{
  text-decoration: none;
  color: #ffffff94;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 13px;

}
.home{
  height: 75vh;
  text-align: center;
  padding: 5vh
}
.head{
  font-size: 22px;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  font-weight: 600;
  padding-bottom: 35px;
}
.c{
  background-color: none;
  border: none;
  background: none;
  cursor: pointer;
}
.abn{
  margin: auto;
  width: 80%;
  height: 60vh;
}
